export default {"name":"北港毛尖","img":"\\huangcha\\beigangmaojian\\img_20191122101844mkfvfp.jpg","desc":["北港毛尖以注册商标“北港”命名，唐代称“邕(yōng)湖茶”，属黄茶类，产于湖南省岳阳市北港。在加工技术中，通过热闷措施的摧化作用，破坏叶绿素，使不溶性碳水化合物部分转化成可溶性，儿茶素类发生非酶性氧化和异构化作用，其转化产物与蛋白质水解的氨基酸结合，产生新的香味物质，达到茶叶黄化，香味鲜醇回甘。"],"menus":[{"name":"北港毛尖茶叶历史","content":[{"text":"北港茶在唐代就很有名气，唐代称“邕湖茶”。唐代斐济《茶述》中列出了十种贡茶，邕湖茶就是其中之一。唐代李肇《国史补》有“岳州有邕湖之含膏”的记载。北宋范致明《岳阳风土记》载：“湖诸山旧出茶，谓之邕湖茶。李肇所谓岳州邕湖之含膏也，唐人极重之。见于篇什，今人不甚种植，惟白鹤僧国有千余本，土地颇类此苑。所出茶，一岁不过一二十两，土人之白鹤茶，味极甘香，非他处草茶可比并，茶园地色亦相类，但土人不甚植尔。”"},{"text":"明代黄一正辑注的《事物绀珠》列出了今茶名99个，古制造茶名101个，其中岳州出的黄翎毛，岳阳出的含膏冷属古制造茶名。明代张谦德《茶经》也有“岳州之黄翎毛、含膏冷”茶的记载。"},{"text":"清代《致富奇书广集》(疑17、18世纪问世)又有“岳州之黄翎毛，岳阳之含膏冷，唐宋时产茶名”的记载。 清代黄本骥《湖南方物志》引用唐释齐己邕湖茶诗一首云：“邕湖为上贡，何以惠寻常。还是诗心苦，堪消蜡面香。碾声通一室，烹色带残阳。若有新春者，西来信勿忘。”"},{"src":"\\huangcha\\beigangmaojian\\img_201911221018449q07f2.jpg"}]},{"name":"北港毛尖茶叶特点","content":[{"text":"外形芽壮叶肥，毫尖显露，呈金黄色;内质香气清高，汤色橙黄，滋味醇厚，叶底肥嫩黄似朵。"},{"src":"\\huangcha\\beigangmaojian\\img_20191122101844x7z6sf.jpg"}]},{"name":"北港毛尖加工工艺","content":[{"text":"1.采摘：清明后5～6天开采，一芽二三叶标准，抢晴天采，不采虫伤叶，不采紫色叶，不采鱼叶，不带蒂把，当日采摘当日制。"},{"text":"2.杀青：俗称锅炒。北港毛尖的杀青颇别致，与一般\n        绿茶\n        和\n        黄茶\n        杀青极不一样。北港北毛尖采用高温投叶，中温长炒老杀的方法。杀青要求锅温200℃以上，投叶2 000克抖炒2分钟后即降温至100℃左右，再炒12～13分钟，茶坯达三四成千时，锅温已逐步降至40℃左右，即转入锅揉。在较长时间的锅炒中，由于茶叶含水量较高，叶绿素破坏相当严重的，为黄茶要求的特有品质奠定了基础。"},{"text":"3.锅揉：杀青后并不出锅，在锅温40℃左右，茶叶含水率为55%左右时转入锅揉，即在锅中边揉边炒。间以解块操作。待茶叶成条索状时，即出锅拍汗。"},{"text":"4.拍汗：所谓拍汗，即将茶坯盛篾簸内，堆积拍紧，上覆棉套，以保温保湿。闷渥30～40分钟，使茶叶回润变黄。"},{"text":"5.复炒复揉：经过拍汗后，将茶坯再投入锅中复炒。锅温60--70℃，边炒边揉，至茶条紧卷，白毫显露，约八成干时出锅摊晾。"},{"text":"6.烘干：摊晾后用炭火烘干。茶叶烘干后必须摊晾，再装箱内严封，使之后熟。经过后熟，芽叶色泽金黄泽润，便成了质优形美的北港毛尖。"},{"src":"\\huangcha\\beigangmaojian\\img_20191122101844z432l8.jpg"}]},{"name":"北港毛尖储藏方法","content":[{"text":"普通密封保鲜法"},{"text":"将买回的茶叶，立即分成若干小包，装于茶叶罐或筒里，最好一次装满并盖上盖，在不用时不要打开，用完把盖盖严。有条件可在茶叶罐内适当放些用布袋装好的生石灰，以起到吸潮和保鲜作用。"},{"text":"真空抽气充氮法"},{"text":"将茶叶装入铝箔与塑料做成的复合袋，采取一次性封闭真空抽气充氮包装贮存，也可适当加入些保鲜剂。一经启封后，最好在短时间内用完，否则时间一长，同样会陈化劣变。"},{"text":"冷库保鲜法"},{"text":"这种大量保鲜贮存法不加任何保鲜剂，纯属物理保鲜，只要库房建筑合理，库温保持在-4℃至2℃不变即可。但茶叶出库前必须要经过真空保鲜处理，否则，茶叶暴露空气中，由于外界冷热相遇，水分和氧气会形成水汽珠而凝结在茶叶上，加速茶叶劣变。"}]},{"name":"北港毛尖冲泡方法","content":[{"text":"冲泡准备"},{"text":"铁制火炉一具，铜质烧壶一把，长方形紫檀木茶盘一个，长方形木制托盘一个，青瓷茶碗若干个(可用三才盖碗杯)，陶质茶罐一个，檀木茶道具一套，茶色茶巾一条，清\n        花茶\n        荷一个，彩瓷花瓶一个、栀子花一枝，香炉一个，香三支，北港毛尖茶若干克。 [9]"},{"text":"冲泡方法"},{"text":"第一道：煮水——邕湖荷露煮含膏"},{"text":"茶者在沏茶前，首先择具备器、挂画、插花、奏乐、焚香静气，神入茶径，然后开始生火煮水。“水为茶之母，茶为水之魂”。烹茶鉴水，乃中国茶道之特色。古人把宜茶之水分为天水、地水，天水即雨、雪、霜、露、雹;地水乃泉、江、河、湖、井。更有趣者，如扫梅花雪、聚腊月霜，集荷叶露，解严冬冰，借清纯之水，发茶人清幽之思。今天我们所烹之水乃北港邕湖荷花晨露，有诗为证：轻纱薄雾锁池塘，滴滴水晶吻异香。集得青荷冰露洁，含膏共煮洗俗肠。"},{"text":"第二道：涤器——青瓷茶碗岳州窑"},{"text":"岳州乃岳阳之古称：唐代不仅有邕湖含膏之名茶，岳州\n        茶具\n        也享有盛名。岳州窑乃全国六大名窑之一，陆羽茶经：四之器》中载：“碗，越州上，鼎州、婺州次，岳州上，寿州、洪州次，越州瓷、岳瓷皆青，青则益茶。”还载有品茶用具二十四件，并提出：“城邑之中，王公之门，二十四器缺一，则茶废矣。”今天我们使用的泡茶之器，乃岳州窑所产的青瓷茶碗，正是名茶用名器，相得益彰。"},{"text":"第三道：赏茶——茶伴公主入青藏"},{"text":"唐代邕湖茶，不仅深受宫庭廷青睐，并为汉藏文化交流之媒介。当年文成公主出嫁西藏，所带物品中的茶叶，就有岳阳名茶——邕湖含膏。并把饮茶习俗传到西藏，茶与佛教融合，布道弘法，形成了西藏喇嘛寺中空前规模的茶之盛会。北港毛尖，芽壮叶肥，毫尖显露，色呈金黄，我们将茶叶给客人一一观赏，恰似茶伴公主入青藏。"},{"text":"第四道：投茶——燕飞寻常百姓家"},{"text":"北港毛尖，昔为皇宫贡品，今为百姓共享。投茶入杯时，茶芽纷纷飘落，恰似紫燕纷飞，正是：旧时王谢堂前燕，飞入寻常百姓家。"},{"text":"第五道：洗茶——横空一鹤排云上"},{"text":"洗茶时：眼观流水，心注于茶，人茶合一，茶心共洗。茶尘涤尽，心烦同失。茶芽舒卷，心襟同阔。杯上白鹤，心与共翔。顷刻大有唐代诗人刘禹锡《秋词》一诗共感：自古逢秋悲寂寞，我言秋日胜春朝。横空一鹤排云上，便引诗情到碧宵。"},{"text":"第六道：泡茶——水润北港瑞草黄"},{"text":"茶以名山秀水为宅，钟山川之灵秀，以清风明月为伴，得天地之精和，以人类为友，得人之灵性，天、地、人三才共同化育出茶之精华，可谓仙草琼浆。北港毛尖，宜用90℃沸水冲泡。泡茶时，水入茶碗，芽叶舒卷，黄汤四溢，正是水润北港瑞草黄。"},{"text":"第七道：敬茶——邕湖含膏惟上贡"},{"text":"茶汤泡好后，将茶一一敬奉给客人，各位将一览名茶风雅，一饱皇家口福。有唐代诗僧齐已《谢人惠邕湖茶》一诗为证：邕湖惟上贡，何以惠寻常。还是诗心苦，堪消蜡面香。碾声通一室，烹色带残阳。若有新春者，西来信勿忘。"},{"text":"第八道：闻香——亦有灵源一派香"},{"text":"北港毛尖产地，茶园地势平坦，水陆交错，土质肥沃，酸度适宜，正是宜茶之地。其茶香气清高，有宋代大儒朱熹《咏茶》 一诗为感：小小茶树数千章，走寄萌芽初得尝。虽无山顶烟岗润，亦有灵源一派香。"},{"text":"第九道：品茶——涤烦解渴心神悦"},{"text":"文成公主出嫁西藏想念家乡，常要大唐使者带去茶叶，并和松赞干布举杯共饮。"},{"text":"《唐国史补》中载：“常鲁公使西蕃，烹茶帐中，赞普问曰：‘此为何物?’鲁公曰‘涤烦解渴，所谓茶也’。赞普曰：‘我此亦有’。遂命出之，以指曰……此邕湖者。”北港毛尖，滋味醇厚，品后神清气爽，愁烦顿失。有朱熹《茶宴、论茗》一诗为感：茗饮沦甘寒，抖擞神气增。顿生尘虑空，豁然悦心目。"},{"text":"第十道：醉茶——夜归踏碎满街月"},{"text":"“醉翁之意不在酒，在乎山水之间。”品茶之韵不在茶，在乎清雅之境。有道是：“有韵则雅，韵致清远。”茶致数巡，身舒神悦，乐哉!悠哉!似醉非醉。至此，大有宋代诗人黄庭坚《一斛珠》一词共感：香芽嫩茶清心骨，醉中襟量与天阔。夜阑似觉归仙阙。走马章台，踏碎满街月。"},{"text":"饮茶禁忌"},{"text":"喝头遍茶：有的人泡茶会将头遍的水滤掉、喝第二道，这是懂喝茶的做法。茶叶在栽培与加工过程中可能会受到农药等有害物的污染，表面会有一定残留，所以头遍的水作为洗涤之用最好弃之不喝。"},{"text":"空腹或饭前喝茶：空腹喝茶对胃的伤害非常大，可使茶叶中的不良成分大量入血，引发头晕、心慌、手脚无力等症状。喝茶时若能搭配一些小点心对肠胃很好。同样的，饭前半小时也不宜喝茶。"},{"text":"饭后立即喝茶：饭后立即喝茶，茶叶中的鞣酸会与食物中的铁元素发生反应，生成难以溶解的新物质，长期可能引起人体缺铁性贫血。所以喝茶最好是在吃完饭半个小时以后。"},{"text":"发烧喝茶：茶叶中含有的茶碱有升高体温的作用，发烧喝茶无异于“火上浇油”。"},{"text":"溃疡喝茶：茶叶中含有的咖啡因会促进胃酸分泌，升高胃酸浓度，引发溃疡甚至穿孔。所以溃疡病人不宜喝茶。"},{"text":"睡前喝茶：如果晚上喝茶离睡觉时间太短的话，容易影响睡眠。"},{"text":"妇女经期，孕期喝茶：会影响内分泌，对喝茶的妇女胎儿造成不好的影响。"},{"text":"喝很浓的茶：茶汤浓度太高，刺激性过大，加重内脏器官的负担并且扰乱内分泌。"}]},{"name":"北港毛尖功效作用","content":[{"text":"中国作为茶的故乡，对于茶的养生之道大家都有所听闻。北港毛尖是\n        黄茶\n        中的一种，具体说来，北港毛尖有8大养生功效。"},{"text":"1、消炎：茶水有收敛、消炎等作用，能预防肠道传染病。"},{"text":"2、茶水可分解烟草中的某些毒素，尤其能抑制尼古丁对人体健康的影响。"},{"text":"3、抗菌、抗病毒：茶水有抗菌、抗病毒、消毒等作用。对清洗小伤口、止血、止痛等作用相当明显。"},{"text":"4、防龋齿：茶水漱口可防止龋齿生成，有利于口腔卫生。"},{"text":"5、煮沸的茶水冷却后，涂在嘴唇疱疹处，4、5天后嘴唇疱疹即可逐渐消失。"},{"text":"6、茶叶有抑制细胞突变与癌变的作用，而且有抑制癌细胞的生长和扩散作用，长期饮茶能降低食管癌、胃癌、肠癌等消化道肿瘤的发病率。"},{"text":"7、茶水可提神醒脑、清热解毒，具有明目、消滞、减肥之功效。"}]},{"name":"北港毛尖历史故事","content":[{"text":"明代黄一正辑注的《事物绀珠》列出了今茶名99个，古制造茶名101个，其中岳州出的黄翎毛，岳阳出的含膏冷属古制造茶名。明代张谦德《茶经》也有“岳州之黄翎毛、含膏冷”茶的记载。清代《致富奇书广集》(疑十七、十八世纪问世)又有岳州邕湖产含膏茶的记载。清代黄本骥《湖南方物志》有“岳州之黄翎毛，岳阳之含膏冷，唐宋时产茶名”的记载。还引用唐释齐己邕湖茶诗一首云：“邕湖为上贡，何以惠寻常，还是诗心苦，堪消蜡面香，碾声通一室，烹色带残阳，若有新春者，西来信勿忘。”"}]},{"name":"北港毛尖获奖情况","content":[{"text":"1964年被评为湖南省优质产品。"}]},{"name":"北港毛尖鉴别好茶","content":[{"text":"北港毛尖鲜叶一般在清明后五六天开园采摘，要求一号毛尖原料为一芽一叶，二、三号毛尖为一芽二、三叶。抢晴天采，不采虫伤、紫色芽叶、鱼叶及蒂把。鲜叶随采随制，其加工方法分锅炒、锅揉、拍汗及烘干四道工序。成品外形呈金黄色，毫尖显露，茶条肥硕，汤色澄黄，香气清高，滋味醇"},{"text":"厚，甘甜爽口。本品在清代乾隆年间已有名气，由岳阳市茶叶公司生产的北港毛尖，于1964年被评为湖南省优质名茶。北港毛尖一般在清明后5─6天开采，鲜叶标准为一芽二、三叶，选晴天采摘。不采虫伤叶、紫色叶、鱼叶，不带蒂把。严格做到随采随制。北港毛尖制造分锅炒、锅揉、拍汗、复炒和烘干五道工序。锅炒和锅揉：锅炒主要起杀青作用。杀青前将锅磨光洗净。开始锅温要高，升温至180℃，每锅投叶1--1.5公斤。鲜叶下锅后，先抖炒2分钟，把锅温降到100℃以下，再炒12--13分钟。当叶子发出清香，茶坯达到三、四成干，再降温到40℃左右，在锅内进行揉炒解块，使茶叶卷成条索，待六成干时，即可出锅拍汗。拍汗：将茶坯放置簸箕内拍紧，上盖棉套，时间30─40分钟。使茶条回润，色泽变黄，再投入锅内复炒复揉复炒：锅温保持在60--070℃，炒至条索紧卷，白毫显露，达八成干时，出锅摊凉。烘干：用木炭烘焙，火温80--90℃，到足干下焙，装入箱内严封，使茶叶色泽进一步转黄;北港毛尖的品质特点是，外形芽壮叶肥，毫尖显露，呈金黄色，汤色橙黄，香气清高，滋味醇厚。产品按老嫩度分特号、1--4号五个档次。"}]}]}